              <path id="girl-skirt" class="st0" d="M667.3,666.5c-70.9,27.4-52.2,153.8-52.2,153.8s71.9,19.6,188.3-57
                C803.4,763.2,748.3,635.2,667.3,666.5L667.3,666.5z"/>
              <path id="girl-arm" ref="girl-arm" class="st1" d="M609.1,652.2c0,0,42.7-48.8,49.7-42.7c7.3,6.5-38.6,52-38.6,52h-76.5L609.1,652.2z"/>
            <g id="girl-head" ref="girl-head">
                      <path id="girl-face" ref="girl-face" class="st1" d="M695.4,509.5c-2-27.6-35.9-47.4-75.5-44.6c-39.6,3.1-70.1,27.7-68,55.1c2,27.6,35.7,47.5,75.3,44.6
                C667,561.7,697.4,537,695.4,509.5L695.4,509.5z"/>
              <path id="girl-eye" ref="girl-eye" class="st3" d="M579.7,507.6c-4.6,0.3-8,4.4-7.7,9c0.3,4.6,4.2,8.2,8.8,7.8c4.8-0.3,8.2-4.4,7.8-9
                C588.4,510.8,584.3,507.3,579.7,507.6L579.7,507.6z"/>
                      <path id="girl-hair" ref="girl-hair" class="st3" d="M561.8,491.5c0,0,25.8,24.7,76.2,33.5c18.5,32.6,57.5,136.6,57.5,136.6
                s41.2,39.8,78.4-1.9c-11.2-4.4-47.4-6.3-61.4-44.4c-13.9-38.3,6.6-90-13.3-125.5c-11.6-20.9-35.4-44.2-75.7-39.4
                C591.8,453.9,576.1,468.7,561.8,491.5L561.8,491.5z"/>
              <path id="girl-earring" ref="girl-earring" class="st4" d="M646.9,525c0,4.8-3.9,8.7-8.8,8.7c-4.8,0-8.7-3.9-8.7-8.7s3.9-8.7,8.7-8.7C643,516.3,646.9,520.2,646.9,525
                L646.9,525z"/>
              </g>
              <polygon class="st1" points="635.2,561.5 642.6,577.5 659.8,569.5 651.1,550.6 			"/>
            </g>
          </g>
          <g id="boy" ref="boy">
            <polygon class="st2" points="481.6,901.5 498.9,901.5 500.8,901.5 500.6,903.2 532.6,925.5 480.4,925.5 480.4,901.5 		"/>
            <rect x="474.8" y="565.8" class="st1" width="16.8" height="13.3"/>
            <polygon class="st1" points="504.4,587.7 484.6,582.8 433.4,643.9 484.6,691.6 453.3,642.2 		"/>
            <path class="st1" d="M478.7,461.9c-41.3,0-75,24-75,53.6c0,29.6,33.7,53.6,75,53.6c41.5,0,75.2-24,75.2-53.6
              C553.9,485.8,520.2,461.9,478.7,461.9L478.7,461.9z"/>
            <path id="boy-eye" ref="boy-eye" class="st3" d="M534.1,503.4c-4.1,0-7.3,3.4-7.3,7.3c0,3.9,3.2,7.1,7.3,7.1c3.9,0,7.1-3.2,7.1-7.1
              C541.3,506.8,538,503.4,534.1,503.4L534.1,503.4z"/>
            <path class="st3" d="M526.8,473.1c0,0-3.2,30.4-57.8,46.6c0,10.5-0.8,48.1-0.8,48.1h-64.5c0,0-26.2-76,41.3-101.4
              C463.4,461.9,489.6,454.9,526.8,473.1L526.8,473.1z"/>
            <path id="boy-hat-2" ref="boy-hat-2" class="st2" d="M379.9,516.6c7.1,12.6,44.9,4.9,81.8-16.3c34.2-20.4,52.7-45.9,43.7-58
              c-8.7-11.9-40-6.5-72.5,13.4C397.9,476.3,373.1,504,379.9,516.6L379.9,516.6z"/>
            <path id="boy-hat-1" ref="boy-hat-1" class="st0" d="M471.2,443.3c-5.1-4.6-13.6-12.2-20.1-11.6c-11.7,1.4-28.2,10-28.2,10s-17.9,9.4-24.1,17.5
              c-4.3,5.1-2.9,16.2-2.2,23c0.8,7,4.2,16.8,4.2,16.8c64.5-4.6,81.8-42.7,81.8-42.7S476.5,447.9,471.2,443.3L471.2,443.3z"/>
            <polygon id="boy-pants" ref="boy-pants" class="st0" points="461.5,704.9 481.6,901.5 498.9,901.5 523.2,704.9 		"/>
            <polygon id="boy-shirt" ref="boy-shirt" class="st2" points="492.8,576 477.1,576 469.2,576 469.2,579 469.2,605.1 461.5,706.1 523.8,706.1
              509.8,576 508.3,576 		"/>
            <polygon class="st1" points="474.6,598.3 514.7,662.4 602.3,667.3 519.8,645.9 490.1,581.6 		"/>
            <path class="st1" d="M480.4,491.6c-6.3,0-11.6,9.7-11.6,21.4c0,11.9,5.3,21.6,11.6,21.6c6.5,0,11.6-9.7,11.6-21.6
              C491.9,501.3,486.8,491.6,480.4,491.6L480.4,491.6z"/>
            <polygon id="boy-sleeve" ref="boy-sleeve" class="st0" points="469.2,594.9 489.7,626.2 513.9,610.3 493.3,579 		"/>
          </g>
        </g>
        <polygon id="triangle-light" ref="light" class="st5" points="1265.8,1383.2 542.5,0 0,1383.2 "/>
      </svg>
    </div>

  </body>

</template>

<script>
import { TimelineMax, Back } from "gsap";

export default {
  data() {
    return {};
  },
  created() {
    const master = new TimelineMax();
    master
      .add(this.setup)
      .add(this.darkness, "lightsOff+=1")
      .add(this.girlKiss, "girlStuff+=1")
      .add(this.boyStuff, "girlStuff+=2")
      .add(this.heartStuff, "heartStuff")
      .add(this.lightsOn, "heartStuff+=2");
  },
  methods: {
    setup() {
      const tl = new TimelineMax();
      tl
        .set(this.$refs.couple, { autoAlpha: 1 })
        .set(this.$refs["girl-earring"], { scaleY: "0.7" })
        .set(this.$refs["girl-leg-1"], { rotation: "+=75" })
        .set(this.$refs["girl-arm"], {
          transformOrigin: "100% 0%",
          rotation: "-=60"
        })
        .set(this.$refs["girl-body"], {
          transformOrigin: "0% 75%",
          rotation: "+=20"
        })
        .set(this.$ref.heart, {
          transformOrigin: "50% 50%",
          y: "+=100",
          scale: 0.5
        });
      return tl;
    },
    darkness() {
      const tl = new TimelineMax();
      tl.to(this.$refs.background, 1, { backgroundColor: "#1a2124" });
      return tl;
    },
    girlKiss() {
      const tl = new TimelineMax();
      tl
        .to(this.$refs["girl-arm"], 1, { rotation: 0 })
        .to(this.$refs["girl-body"], 0.5, {
          transformOrigin: "0% 75%",
          rotation: 0
        })
        .to(
          this.$refs["girl-eye"],
          0.5,
          { transformOrigin: "0% 50%", scaleY: 0.1 },
          1.25
        )
        .to(
          this.$refs["girl-leg-1"],
          1.5,
          { rotation: 0, ease: Back.easeOut.config(3) },
          1.25
        );

      return tl;
    },
    boyStuff() {
      const tl = new TimelineMax();
      tl.to(this.$refs["boy-eye"], 0.5, {
        transformOrigin: "0% 50%",
        scaleY: 0.1
      });

      return tl;
    },
    heartStuff() {
      const tl = new TimelineMax();
      tl.to(this.$refs.heart, 1, { autoAlpha: 1, y: 0, scale: 1 });
      return tl;
    },
    lightsOn() {
      const tl = new TimelineMax();
      tl
        .to(this.$refs.light, 0.25, { autoAlpha: 1 }, 0)
        .to(this.$refs.ground, 0.25, { fill: "#F8E5C5" }, 0)
        .to(this.$refs["girl-eye"], 0.5, { scaleY: 1 }, 0.5)
        .to(this.$refs["boy-eye"], 0.5, { scaleY: 1 }, 1)
        .to(this.$refs["girl-leg-1"], 1, { rotation: "+=75" })
        .to(this.$refs["girl-body"], 0.5, { rotation: "+=20" });

      return tl;
    }
  }
};
</script>


<style scoped>
.st0 {
  fill: #f8e5c5;
}
.st1 {
  fill: #f0b38a;
}
.st2 {
  fill: #660000;
}
.st3 {
  fill: #161c1d;
}
.st4 {
  fill: #eb1d23;
}
.st5 {
  fill: url(#triangle-light_4_);
  fill-opacity: 0.3;
}
.st6 {
  fill: url(#triangle-light_5_);
  fill-opacity: 0.3;
}
*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  background-image: radial-gradient(
    1600px at 70% 120%,
    rgba(33, 39, 80, 1) 10%,
    #020409 100%
  );
  overflow: hidden;
}

.container {
  height: 100vh;
  overflow: hidden;
}
#couple {
  max-height: 100%;
  width: 100%;
}

#girl-hair {
  fill: url(#hair-gradient);
}
#girl-eye {
  fill: green;
}

#girl-earring {
  fill: #8d8d8d;
}
#girl-skirt,
#girl-shirt {
  fill: #791e43;
}
#boy-pants {
  fill: #848487;
}
#boy-sleeve {
  fill: #b8bcbb;
}

#boy-shirt {
  fill: #256e89;
}

#boy-hat-1 {
  fill: #2e6773;
}
#boy-hat-2 {
  fill: #4f3723;
}

#couple,
#heart,
#triangle-light {
  visibility: hidden;
}
#triangle-light {
  fill: url(#light-gradient);
  fill-opacity: 0.3;
}

#ground {
  fill: #5c5757;
}

.heart {
  position: relative;
  width: 100px;
  height: 90px;
}
.heart:before,
.heart:after {
  position: absolute;
  content: "";
  left: 50px;
  top: 0;
  width: 50px;
  height: 80px;
  background: red;
  -moz-border-radius: 50px 50px 0 0;
  border-radius: 50px 50px 0 0;
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
  -webkit-transform-origin: 0 100%;
  -moz-transform-origin: 0 100%;
  -ms-transform-origin: 0 100%;
  -o-transform-origin: 0 100%;
  transform-origin: 0 100%;
}
.heart:after {
  left: 0;
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
  -webkit-transform-origin: 100% 100%;
  -moz-transform-origin: 100% 100%;
  -ms-transform-origin: 100% 100%;
  -o-transform-origin: 100% 100%;
  transform-origin: 100% 100%;
}
</style>



